import React from "react";
import "../Home.css";
import Product from "../Product";
import img1 from "./women1.jpg";
import img2 from "./women2.jpg";




function Seniorcitizen() {
  return (
    <div className="home">
      <div className="home__container">
     
        
        <h1 className="tags1">Help your grandparents</h1>
        <div className="home__row">
          <Product
            id="1"
            title="Feed a grandparent"
            price={500}
           
            image="https://cdn.givind.org/static/images/program/w450/feed-a-grandparent-through-a-community-kitchen.jpg"
        />
          <Product
            id="2"
            title="Gift a grandma hope in her last days"
            price={300}
           
            image="https://cdn.givind.org/static/images/program/w450/gift-a-grandma-hope-in-her-last-days.jpg"
        />
        </div>
        <div className="home__row">
         <Product
            id="2"
            title="Adopt a grandparent by providing them monthly groceries"
            price={300}
           
            image="https://cdn.givind.org/static/images/program/w450/sponsor-the-food-and-medical-needs-of-a-poor-senior-citizen.jpg"
        />
        
        </div>

       
        
      </div>
    </div>
  );
}

export default Seniorcitizen;
