import React from 'react';
import Navbar from './Navbar';
// import './Cards.css';
import CardItem2 from './carditem2';
import plant from './assets/plant.png';
import children from "./assets/children.png";
import women from './assets/women.png';
import senior from './assets/senior.png';
import animal from './assets/animal.png';
import disaster from './assets/disaster.png';

import Footer from './Footer';
// import './Cards.css';


function Categories() {
    return (
        <div>
            {/* <div className="Head" >
         
            </div> */}
           
            <div className='cards'>
            <div className='cards__container'>
            <div className='cards__wrapper'>
            <h1 className="tags1">Categories</h1> 
            <ul className='cards__items'> 
  
            
            
            <CardItem2
            src={children}
            text='Child Welfare'
            path="/child"
            />
            <CardItem2
            src={women}
            text='Women Health and Safety'
            path="/women"
            />
             <CardItem2
            src={senior}
            text='Senior Citizen'
            path="/seniorcitizens"
            />

          </ul>
          <ul className='cards__items'> 
          <CardItem2
             src={plant}
             text='Donation For plantation'
             path="/enviornment"
            />
           <CardItem2
            src={animal}
            text='Animal Welfare'
            path="/animalwelfare"
            />
            
            <CardItem2
            src={disaster}
            text='Donation for Disasters'
            path="/disaster"
            />
            </ul>
            </div>
           </div>
           </div>
           
        </div>
    )
}

export default Categories
