import React from "react";
import "../Home.css";
import Product from "../Product";
import img1 from "./women1.jpg";
import img2 from "./women2.jpg";




function Animalwelfare() {
  return (
    <div className="home">
      <div className="home__container">
     
        
        <h1 className="tags1">Animal Welfare</h1>
        <div className="home__row">
          <Product
            id="1"
            title="Food for street dogs"
            price={50}
           
            image="https://thumbs.dreamstime.com/b/stray-dog-eating-food-scraps-70386223.jpg"
        />
          <Product
            id="2"
            title="Medicine for Dogs"
            price={15}
           
            image="https://blogs.headsupfortails.com/wp-content/uploads/2014/03/Street-dog-with-mange.jpg"
        />
        
        </div>

       
        
      </div>
    </div>
  );
}

export default Animalwelfare;
