import React from "react";
import "./Home.css";
import Product from "./Product";
import img1 from "./merch1.jpg";
import img2 from "./merch2.jpg";



function Merch() {
  return (
    <div className="home">
      <div className="home__container">
     
        
        <h4 className="tags1">Profits of our Products goes to charity </h4>
        <div className="home__row">
            <Product
                id="123"
                title="Tee-shirts for helpers"
                price={500}
            
                image={img1}
            />
            <Product
                id="123456"
                title="Sticker For Donators"
                price={40}
            
                image="https://static.vecteezy.com/system/resources/previews/001/594/512/original/cute-donation-sticker-collection-free-vector.jpg"
            />
            </div>
            <div className="home__row">
            <Product
                id="123456789"
                title="Free Goodies for Donators"
                price={0}
            
                image={img2}
            />
            
            </div>
          
        
      </div>
    </div>
  );
}

export default Merch;
